:root {
  --base-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;

  --base-color-primary-hue: 250;
  --base-color-primary-light: hsl(var(--base-color-primary-hue), 100%, 90%);
  --base-color-primary: hsl(var(--base-color-primary-hue), 100%, 60%);
  --base-color-primary-dark: hsl(var(--base-color-primary-hue), 100%, 50%);

  --base-color-secondary-hue: 250;
  --base-color-secondary-light: hsl(var(--base-color-secondary-hue), 100%, 95%);
  --base-color-secondary: hsl(var(--base-color-secondary-hue), 100%, 95%);
  --base-color-secondary-dark: hsl(var(--base-color-secondary-hue), 100%, 90%);

  --base-color-focus-light: var(--base-color-primary-light);
  --base-color-focus: var(--base-color-primary);
  --base-color-focus-dark: var(--base-color-primary-dark);

  --base-color-success-hue: 140;
  --base-color-success: hsl(var(--base-color-success-hue), 100%, 40%);
  --base-color-success-dark: hsl(var(--base-color-success-hue), 100%, 30%);

  --base-color-danger-hue: 0;
  --base-color-danger: hsl(var(--base-color-danger-hue), 100%, 40%);
  --base-color-danger-dark: hsl(var(--base-color-danger-hue), 100%, 30%);

  --base-color-white: white;
  --base-color-black: black;

  --base-color-ui-hue: 0;
  --base-color-ui-lightest: hsl(var(--base-color-ui-hue), 0%, 95%);
  --base-color-ui-lighter: hsl(var(--base-color-ui-hue), 0%, 90%);
  --base-color-ui-light: hsl(var(--base-color-ui-hue), 0%, 80%);
  --base-color-ui: hsl(var(--base-color-ui-hue), 0%, 70%);
  --base-color-ui-dark: hsl(var(--base-color-ui-hue), 0%, 30%);
  --base-color-ui-darker: hsl(var(--base-color-ui-hue), 0%, 20%);
  --base-color-ui-darkest: hsl(var(--base-color-ui-hue), 0%, 10%);

  --base-color-font-light: hsl(var(--base-color-ui-hue), 0%, 45%);
  --base-color-font: hsl(var(--base-color-ui-hue), 0%, 25%);
  --base-color-font-dark: hsl(var(--base-color-ui-hue), 0%, 5%);

  --base-container: 800px;
  --base-container-xs: 600px;
  --base-container-sm: 800px;
  --base-container-md: 1000px;
  --base-container-lg: 1200px;
  --base-container-xl: 1400px;

  --base-space-xs: 4px;
  --base-space-sm: 8px;
  --base-space-md: 16px;
  --base-space-lg: 32px;
  --base-space-xl: 64px;

  --base-font-size-xs: 0.83em;
  --base-font-size-sm: 1em;
  --base-font-size-md: 1.17em;
  --base-font-size-lg: 1.5em;
  --base-font-size-xl: 2em;

  --base-border-radius-xs: 2px;
  --base-border-radius-sm: 4px;
  --base-border-radius-md: 8px;
  --base-border-radius-lg: 16px;
  --base-border-radius-xl: 32px;

  --base-size: 48px;
  --base-size-xs: 24px;
  --base-size-md: 48px;
  --base-size-sm: 36px;
  --base-size-lg: 58px;

  /* DEPTH */
  --base-depth-none: none;
  --base-depth-xs: 0px 1px 1px 0px rgba(9, 30, 66, 0.06),
    0px 2px 4px 0px rgba(9, 30, 66, 0.15);
  --base-depth-sm: 0px 2px 2px 0px rgba(9, 30, 66, 0.06),
    0px 4px 8px -2px rgba(9, 30, 66, 0.25);
  --base-depth-md: 0px 2px 2px 0px rgba(9, 30, 66, 0.06),
    0px 8px 16px -4px rgba(9, 30, 66, 0.25);
  --base-depth-lg: 0px 4px 4px 0px rgba(9, 30, 66, 0.06),
    0px 12px 24px -6px rgba(9, 30, 66, 0.25);
  --base-depth-xl: 0px 6px 6px 0px rgba(9, 30, 66, 0.1),
    0px 20px 32px -8px rgba(9, 30, 66, 0.25);
}

[theme="minimal"] {
  transition: background 0.2s ease;
  color: var(--base-color-font);
  font-family: var(--base-font-family);
  background-color: var(--base-color-white);
}

[mode="dark"] {
  --base-color-primary-hue: 400;
  --base-color-primary: hsl(var(--base-color-primary-hue), 100%, 50%);
  --base-color-primary-light: hsl(var(--base-color-primary-hue), 100%, 70%);
  --base-color-primary-dark: hsl(var(--base-color-primary-hue), 100%, 60%);

  --base-color-focus-light: var(--base-color-primary-light);
  --base-color-focus: var(--base-color-primary);
  --base-color-focus-dark: var(--base-color-primary-dark);

  --base-color-secondary-hue: 400;
  --base-color-secondary: hsl(var(--base-color-secondary-hue), 100%, 95%);
  --base-color-secondary-dark: hsl(var(--base-color-secondary-hue), 100%, 90%);

  --base-color-success-hue: 140;
  --base-color-success: hsl(var(--base-color-success-hue), 100%, 40%);
  --base-color-success-dark: hsl(var(--base-color-success-hue), 100%, 30%);

  --base-color-danger-hue: 0;
  --base-color-danger: hsl(var(--base-color-danger-hue), 100%, 40%);
  --base-color-danger-dark: hsl(var(--base-color-danger-hue), 100%, 30%);

  --base-color-white: black;
  --base-color-black: white;

  --base-color-ui-hue: 0;
  --base-color-ui-lightest: hsl(var(--base-color-ui-hue), 0%, 20%);
  --base-color-ui-lighter: hsl(var(--base-color-ui-hue), 0%, 30%);
  --base-color-ui-light: hsl(var(--base-color-ui-hue), 0%, 45%);
  --base-color-ui: hsl(var(--base-color-ui-hue), 0%, 60%);
  --base-color-ui-dark: hsl(var(--base-color-ui-hue), 0%, 80%);
  --base-color-ui-darker: hsl(var(--base-color-ui-hue), 0%, 90%);
  --base-color-ui-darkest: hsl(var(--base-color-ui-hue), 0%, 95%);

  --base-color-font-light: hsl(var(--base-color-ui-hue), 0%, 70%);
  --base-color-font: hsl(var(--base-color-ui-hue), 0%, 80%);
  --base-color-font-dark: hsl(var(--base-color-ui-hue), 0%, 100%);
}

[mode="dark"] base-button[type="secondary"]::part(button) {
  --base-button-bg-color: var(--base-color-black);
  --base-button-text-color: var(--base-color-white);
}

[mode="dark"] base-button[type="secondary"]::part(button):hover {
  --base-button-bg-color: var(--base-color-black);
  --base-button-text-color: var(--base-color-white);
}
